import Vue from "vue";
import Vuex from "vuex";

Vue.use(Vuex);

export default new Vuex.Store({
  state: {
    currentCont: null,
  },
  mutations: {
    setCont(state, data) {
      state.currentCont = data;
    },
  },
  getters: {
    currentCont(state) {
      return state.currentCont;
    },
  },
});
